/*FLIPPER*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1{
    font-size: 2.5rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: normal;
    color: #444;
    text-align: center;
    margin: 2rem 0;
}

.profile-wrapper{
    width: 90%;
    margin: 0 auto;
    max-width: 80rem;
    margin-top: 50px;
}

.profile-cols{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.profile-col{
    width: 100%;
    padding: 0 !important;
}

.profile-layout-container{
    transform-style: preserve-3d;
    perspective: 1000px;
    /* FIXME: cursor: pointer; */

}
.profile-front,
.profile-back{
    background-size: cover;
    background-position: center;
    transition: transform .7s cubic-bezier(0.4, 0.2, 0.2, 1);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    text-align: center;
    min-height: 400px;
    height: auto;
    border-radius: 10px;
    color: #fff;
    font-size: 1.5rem;
}

.profile-back{
    background: #1F2137;
}

.profile-front:after{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    content: '';
    display: block;
    opacity: .6;
    /*background-color: #000;*/
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 10px;
}
.profile-layout-container:hover .profile-front,
.profile-layout-container:hover .profile-back{
    transition: transform .7s cubic-bezier(0.4, 0.2, 0.2, 1);
}

.profile-back{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
.title-inner{
    margin: 0;
    font-weight: 200;
    font-family: "Calling Code";
    font-size: 50px;
}
.role-inner{
    margin: 0;
}

.profile-inner{
    transform: translateY(-50%) translateZ(60px) scale(0.94);
    top: 50%;
    position: absolute;
    left: 0;
    width: 100%;
    padding: 2rem;
    box-sizing: border-box;
    outline: 1px solid transparent;
    perspective: inherit;
    z-index: 2;
}

.profile-layout-container .profile-back{
    transform: rotateY(180deg);
    transform-style: preserve-3d;
}

.profile-layout-container .profile-front{
    transform: rotateY(0deg);
    transform-style: preserve-3d;
}

.profile-layout-container:hover .profile-back{
    transform: rotateY(0deg);
    transform-style: preserve-3d;
}

.profile-layout-container:hover .profile-front{
    transform: rotateY(-180deg);
    transform-style: preserve-3d;
}

.profile-front .profile-inner p{
    font-size: 2rem;
    margin-bottom: 2rem;
    position: relative;
}

.profile-front .profile-inner p:after{
    content: '';
    width: 4rem;
    height: 2px;
    position: absolute;
    background: #C6D4DF;
    display: block;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: -.75rem;
}

.profile-front .profile-inner span{
    color: rgba(255,255,255,0.7);
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
}
